<template functional>
    <a-sub-menu :key="props.menuInfo.path">
        <span slot="title">
            <a-icon v-if="props.menuInfo.meta.icon" :type="props.menuInfo.meta.icon"></a-icon>
            <span>{{props.menuInfo.meta.title}}</span>
        </span>
        <template v-for="item in props.menuInfo.children">
            <a-menu-item v-if="!item.children" :key="item.path" @click="()=> parent.$router.push({path: item.path, query: parent.$route.query})">
                <a-icon v-if="item.meta.icon" :type="item.meta.icon"></a-icon>
                <span>{{item.meta.title}}</span>
            </a-menu-item>
            <sub-menu v-else :key="item.key" :menu-info="item"></sub-menu>
        </template>
    </a-sub-menu>
</template>

<script>
export default {
    props: ["menuInfo"],
}
</script>

